/*------------------------------------
  Button Sizes
------------------------------------*/
.btn-xs:not(.btn-icon) {
  font-size: 0.75rem;
  padding: 0.3125rem 0.75rem;
}

.btn-icon.btn-xs {
  font-size: 0.75rem;
  width: 1.75rem;
  height: 1.75rem;
}
.btn-icon.btn-xs > svg {
  width: 0.75rem;
  height: auto;
}

.btn-icon.btn-sm, .btn-group-sm > .btn-icon.btn {
  font-size: 0.8125rem;
  width: 2.5rem;
  height: 2.5rem;
}
.btn-icon.btn-sm > svg, .btn-group-sm > .btn-icon.btn > svg {
  width: 0.8125rem;
  height: auto;
}

.btn-icon.btn-lg, .btn-group-lg > .btn-icon.btn {
  font-size: 1.25rem;
  width: 4.25rem;
  height: 4.25rem;
}
.btn-icon.btn-lg > svg, .btn-group-lg > .btn-icon.btn > svg {
  width: 1.25rem;
  height: auto;
}

/*------------------------------------
  Card
------------------------------------*/
.card {
  border-width: 0;
  box-shadow: 0rem 0.375rem 1.5rem 0rem rgba(140, 152, 164, 0.125);
}
.card table tr th {
  font-weight: 400;
}

.card-header {
  border-width: 0;
}

.card-footer {
  border-top-width: 0;
}

.card-subtitle {
  display: block;
  text-transform: uppercase;
  font-size: 0.8125rem;
  margin-bottom: 0.25rem;
}

.card-title {
  margin-bottom: 0;
}
.card-title:not(:last-child) {
  margin-bottom: 0.5rem;
}

.card-header-title {
  margin-bottom: 0;
}
.card-header-title + .card-text {
  margin-top: 0.25rem;
}

.card-link {
  font-weight: 600;
  white-space: nowrap;
}

.card-text {
  color: #677788;
}

.card-bordered {
  border-width: 0.0625rem;
}

.card-transition {
  transition: all 0.2s ease-in-out;
}
.card-transition:hover, .card-transition:focus {
  transform: translateY(-0.1875rem) !important;
}

.card-ghost {
  background-color: transparent;
  box-shadow: none;
}

.card-dashed {
  border: 0.0625rem dashed rgba(33, 50, 91, 0.1);
}

.card-centered .card-body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card-group .card {
  box-shadow: none;
}
.card-group .card + .card {
  border-left: 0.0625rem solid rgba(33, 50, 91, 0.1);
}

.card .card-header + .table-responsive {
  margin-top: -1px;
}
.card .table {
  margin-bottom: 0;
}
.card .card-table > tbody > tr:first-child > td {
  padding-top: 2rem;
}
.card .card-table > tbody > tr:last-child > td {
