.list-pointer-sm .list-pointer-item {
  padding-left: 1.5rem;
}
.list-pointer-sm .list-pointer-item::before {
  width: 1rem;
  height: 1rem;
  background-size: 1rem 1rem;
  margin-top: 0.3125rem;
}
.list-pointer-sm[class*=list-pointer-bg-] .list-pointer-item::before, .list-pointer-sm[class*=list-pointer-soft-bg-] .list-pointer-item::before {
  margin-top: 0.25rem;
}

.list-pointer-lg .list-pointer-item {
  padding-left: 2.75rem;
}
.list-pointer-lg .list-pointer-item:not(:last-child) {
  margin-bottom: 1.25rem;
}
.list-pointer-lg .list-pointer-item::before {
  width: 1.75rem;
  height: 1.75rem;
  background-size: 1.75rem 1.75rem;
  margin-top: 0;
}
.list-pointer-lg[class*=list-pointer-bg-] .list-pointer-item::before, .list-pointer-lg[class*=list-pointer-soft-bg-] .list-pointer-item::before {
  margin-top: -0.0625rem;
}

/*------------------------------------
  Media Viewer
------------------------------------*/
.media-viewer {
  position: relative;
  display: block;
}
.media-viewer:hover .media-viewer-icon {
  opacity: 1;
  transform: scale(1);
}

.media-viewer-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.media-viewer-icon {
  display: inline-flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  text-align: center;
  width: 1.75rem;
  height: 1.75rem;
  font-size: 0.75rem;
  color: #fff;
  background-color: #377dff;
  border-radius: 50%;
  opacity: 0;
  transform: scale(0.7);
  transition: 0.3s;
}
.media-viewer-icon:hover, .media-viewer-icon:focus {
  color: #fff;
}

.media-viewer-icon-active {
  opacity: 1;
  transform: scale(1);
}

/*------------------------------------
  Modal
------------------------------------*/
.modal-header {
  align-items: center;
  padding-bottom: 0;
}
.modal-header .close {
  padding: 0.25rem 0.25rem;
  margin: 0 0 0 auto;
}

.modal-footer {
  border-top-width: 0.0625rem;
}
.modal-footer > * {
  margin-top: 0;
  margin-bottom: 0;
}

.modal-footer-text:last-child {
  font-size: 0.875rem;
  margin-bottom: 0;
}

.modal-close {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  z-index: 2;
}

.modal-top-cover {
  position: relative;
  overflow: hidden;
  min-height: 8rem;
  border-top-right-radius: 0.6875rem;
  border-top-left-radius: 0.6875rem;
}

.modal-top-cover-avatar {
  position: relative;
  z-index: 2;
  margin-top: -3rem;
}

/*------------------------------------
  Nav
------------------------------------*/
.nav-subtitle {
  display: block;
  color: #8c98a4;
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.03125rem;
}

.nav-link.dropdown-toggle {
  align-items: center;
}

.nav-link.active {
  color: #377dff;
}

.nav-link.active .nav-link-svg path,
.nav-item.show .nav-link-svg path {
  fill: #377dff;
}

.nav-title:last-child,
.nav-text:last-child {
  margin-bottom: 0;
}

.nav-icon {
  opacity: 0.7;
  flex: 0 0 1.9375rem;
}

.nav.nav-link-gray .nav-link {
  color: #677788;
}
.nav.nav-link-gray .nav-link.active, .nav.nav-link-gray .nav-link:hover {
  color: #377dff;
}
.nav.nav-link-gray .nav-link.disabled {
  color: #bdc5d1;
}

.nav-tabs .nav-link {
  border-top-width: 0;
  border-left-width: 0;
  border-right-width: 0;
  border-bottom: 0.1875rem solid transparent;
}
.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
  font-weight: 600;
}
.nav-tabs:not(.nav-vertical) .nav-link {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

.nav-vertical {
  flex-direction: column;
}
.nav-vertical.nav .nav-item:not(:last-child) {
  margin-right: 0;
}
.nav-vertical.nav .nav-link {
  display: flex;
  white-space: nowrap;
}
.nav-vertical .nav-subtitle {
  padding-left: 1rem;
}
.nav-vertical.nav-tabs {
  border-bottom-width: 0;
}
.nav-vertical.nav-tabs .nav-link {
  border-top-width: 0;
  border-right-width: 0;
  border-bottom-width: 0;
  border-left: 0.1875rem solid transparent;
  margin-bottom: 0;
}
.nav-vertical.nav-tabs .nav-subtitle {
  border-left: 0.1875rem solid rgba(33, 50, 91, 0.1);
}
.nav-vertical.nav-tabs .nav-link.active,
.nav-vertical.nav-tabs .nav-item.show .nav-link {
  border-color: #377dff;
}
.nav-vertical.nav.nav-tabs .nav-collapse .nav-link {
  padding-left: 3rem;
}
.nav-vertical.nav.nav-tabs .nav-collapse .nav-collapse .nav-link {
  padding-left: 4.5rem;
}
.nav-vertical.nav-pills .nav-link.active,
.nav-vertical.nav-pills .show > .nav-link {
  color: #21325b;
  background-color: transparent;
}
.nav-vertical.nav-pills .nav-link {
  font-size: 0.875rem;
  padding: 0.5rem 1rem;
  margin-bottom: 0.25rem;
}
.nav-vertical.nav-pills .nav-link.active {
  background-color: rgba(189, 197, 209, 0.2);
}
.nav-vertical.nav-pills .nav-link:hover, .nav-vertical.nav-pills .nav-link:focus {
  color: #21325b;
  background-color: rgba(189, 197, 209, 0.2);
}
.nav-vertical.nav-pills .nav-link[aria-expanded=true] {
  color: #377dff;
}
.nav-vertical.nav-pills .nav-collapse {
  position: relative;
  padding-left: 1.9375rem;
}
.nav-vertical.nav-pills .nav-collapse::before {
  position: absolute;
  top: 0;
  left: 1.3125rem;
  width: 0.125rem;
  height: calc(100% - 0.25rem);
  content: "";
  background-color: rgba(33, 50, 91, 0.1);
}
.nav-vertical.nav-pills .nav-link.dropdown-toggle {
  display: flex;
  width: 100%;
}

.nav-pills .nav-item {
  margin: 0.25rem 0.25rem;
}
.nav-pills .nav-link {
  padding: 1rem 1rem;
}
.nav-pills .nav-link:hover {
  color: #fff;
}
.nav-pills .nav-link.active:hover,
.nav-pills .show > .nav-link:hover {
  border-color: transparent;
}

.nav-pills-shadow .nav-link.active {
  box-shadow: 0rem 0.1875rem 0.375rem rgba(140, 152, 164, 0.25);
}

.nav-segment {
  position: relative;
  background-color: #f8fafd;
  padding: 0.25rem 0.25rem;
  border-radius: 0.5rem;
}
.nav-segment:not(.nav-fill) {
  display: inline-flex;
}
.nav-segment .nav-link {
  color: #677788;
  font-size: 0.875rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}
.nav-segment .nav-link:hover {
  color: #377dff;
}
.nav-segment .nav-link.active {
  color: #1e2022;
  background-color: #fff;
  box-shadow: 0rem 0.1875rem 0.375rem 0rem rgba(140, 152, 164, 0.25);
}
.nav-segment .nav-link.disabled {
  color: #bdc5d1;
}
.nav-segment.nav-pills {
  border-radius: 50rem;
}
.nav-segment.nav-pills .nav-link {
  border-radius: 50rem;
}

.nav-light .nav-link {
  color: rgba(255, 255, 255, 0.7);
}
.nav-light .nav-link.active, .nav-light .nav-link:hover {
  color: #fff;
}
.nav-light.nav-pills .nav-link:hover {
  background-color: rgba(189, 197, 209, 0.2);
}
.nav-light.nav-pills .nav-link.active, .nav-light.nav-pills .nav-link:hover {
  color: #fff;
}

.nav-sm .nav-link {
  font-size: 0.9375rem;
  padding-right: 0.75rem;
  padding-left: 0.75rem;
}
.nav-sm.nav-pills .nav-subtitle {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}
.nav-sm.nav-pills .nav-link {
  padding: 0.5rem 0.75rem;
}

.nav-lg .nav-link {
  font-size: 1rem;
}
.nav-lg.nav-pills .nav-link {
  padding: 1.5rem 1.5rem;
}

.nav-link-badge {
  margin-left: auto;
}

/*------------------------------------
  Navbar
------------------------------------*/
.navbar {
  display: block;
  z-index: 99;
}

.navbar-collapse {
  align-items: flex-start;
}

.navbar-nav {
  flex-grow: 1;
}

.navbar-shadow {
  box-shadow: 0rem 0.375rem 1.5rem 0rem rgba(140, 152, 164, 0.125);
}

.navbar-height {
  height: 3.875rem;
}

.navbar.navbar-scrolled.navbar-light {
  background-color: #fff;
  box-shadow: 0rem 0.375rem 1.5rem 0rem rgba(140, 152, 164, 0.125);
}
.navbar.navbar-scrolled.navbar-dark {
  background-color: #21325b;
}

.navbar-brand {
  padding-top: 0;
  padding-bottom: 0;
}

.navbar-brand-logo {
  width: 100%;
  min-width: 7.5rem;
  max-width: 7.5rem;
}

.navbar-brand-collapsed,
.navbar-brand-on-scroll {
  display: none;
}

.navbar-scrolled .navbar-brand-default {
  display: none;
}
.navbar-scrolled .navbar-brand-on-scroll {
  display: inline-block;
}

.navbar-nav-wrap {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 100%;
  align-items: center;
}
.navbar-nav-wrap .navbar-toggler {
  margin-left: auto;
}

.navbar-nav-wrap-secondary-content {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.navbar-toggler {
  padding: 0.5rem 0.5rem;
}
.navbar-toggler:focus {
  box-shadow: none;
}
.navbar-toggler .navbar-toggler-text {
  color: #21325b;
}
.navbar-toggler .navbar-toggler-default {
